.project .Top {
    width: 100%;
    min-width: 350px;
    height: 20px;
    background-color: #15151540;
}

.project .bottom {
    height: 50px;
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: #15151540;
}

.project {
    position: relative;
    height: 320px;
}

.project .Tab {
    width: 38%;
    height: 0.9em;
    margin-left: 20px;
    transform: scale(1.5, 1.43);
    fill: white;
}

.Tabs {
    position: relative;
}

.Tabs .cross {
    position: absolute;
    top: 6px;
    left: 38%;
    height: 0.65em;
}

.Tabs .plus {
    position: absolute;
    top: 5px;
    left: 46%;
    height: 0.65em;
}

.Tabs .icons {
    position: absolute;
    top: 5px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-right: 5px;
    padding-right: 10px;
    height: 0.65em;
}

.Tabs .icon {
    margin-left: 10px;
    height: 0.65em;
}

.project-content {
    height: 250px;
    background-color: white;
    width: 100%;
    min-width: 350px;
    display: flex;
    position: relative;
}

.project .img {
    height: 200px;
    margin: auto;
    display: flex;
    justify-content: center;
}

.project img {
    max-height: 200px;
    max-width: 350px;
    object-fit: contain;
}

.Tabs .title {
    position: absolute;
    font-size: 0.6em;
    top: 3px;
    left: 25px;
    z-index: 20;
    color: grey;
    width: 100px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    text-align: center;
}

.hidden {
    display: none;
}